@import url(https://fonts.googleapis.com/css2?family=Caveat&family=Staatliches&display=swap);
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Caveat', cursive;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-weight: 400;
  font-size: 1.6rem;
  line-height: 1.7;
  color: #334747;
}


code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/*
Colour Scheme:
  Pastel red: #F78888
  Mustard: #F3D250
  Light gray: #ECECEC
  Baby blue: #90CCF4
  Sky Blue: #5DA2D5
  Dark grey: #334747
*/


.content{
  position: absolute;
  top: 5rem;
  padding: 5rem 0;
  width: 100%;
  text-align: center;
  background-image: url(/KTJ-module3/static/media/bg1.2a1f74ba.jpg);
  background-size: cover;
}


.Navbar{
    height: 5rem;
    width: 100%;
    position: fixed;
    font-family: 'Staatliches', cursive;
    background-color: #ECECEC;
    z-index: 1;
    box-shadow: 0 1rem 3rem;
}

.logo-img{
    height: 4rem;
    float: left;
    margin-top: 1vh;
}

.links{
    display:flex;
    flex-wrap: nowrap;
    justify-content: flex-end;
    float: right;
}

.links a{
    color: #334747;
    text-decoration: none;
    font-size: 2rem;
    display: inline-block;
    margin: auto 1.5rem;
    padding-top: 1.4rem;
    height: 5rem;
    position: relative;
    transition: all 0.3s ease-in;
  }

.links a:hover{
    color: #5DA2D5;
}

.links a::before{
    content: "";
    position: absolute;
    display: block;
    width: 100%;
    height: 0.3rem;
    bottom: 0;
    background-color: #5DA2D5;
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
    transition: -webkit-transform 0.3s ease;
    transition: transform 0.3s ease;
    transition: transform 0.3s ease, -webkit-transform 0.3s ease;
}

.links a:hover::before{
    -webkit-transform: scaleX(1);
            transform: scaleX(1);
}

@media only screen and (max-width: 500px){
    .Navbar{
        display: flex;
        flex-wrap: nowrap;
    }
    .logo-img{
        height: 3rem;
    }
    .links a{
        font-size: 1.5rem;
        margin: auto 1rem;
    }
}
.postField, .container{
    text-align: center;
    display: inline-block;
    flex-direction: column;
    margin: 2rem auto;
    padding: 2rem;
    width: 60%;
    background-color: #F78888;
    border-radius: 3rem;
    font-size: 2rem;
}

.control-form{
    font-size: 2rem;
    list-style-type: none;
}

.control-form li{
    padding-bottom: 2rem;
    color: #334747;
    color: white;
    margin: auto;
}

input[type=text]{
    border: 0.3rem solid white;
    border-radius: 0.4rem;
    color: #334747b9;
    padding: 0.35rem;
    opacity: 0.8;
}

.post-field{
    height: 5rem;
    width: 60%;
    min-width: 15rem;
}

.username-field{
    width: 80%;
}

input:focus{
    outline: none;
    opacity: 1.0;
    background-color: rgba(255, 255, 255, 0.600);
}

img:hover{
    cursor: pointer;
}

.post-btn{
    border: 0.5rem solid white;
    border-radius: 0.4rem;
    background-color: #F78888;
    color: white;
    font-weight: bold;
    width: 10rem;
    height: 5rem;
    opacity: 1.0;
}

.post-btn:hover{
    border: 0.5rem solid #9aa1a1;
    background-color: white;
    color: #9aa1a1;
    opacity: 0.9;
}

.post-btn:active{
    border: 0.5rem solid #737979;
    background-color: white;
    color: #737979;
    padding: 0.4rem;
}

.msg{
    font-size: 2rem;
    background-color: rgba(144, 204, 244, 0.50);
}

.success{
    color: rgb(3, 145, 3);
}

.error{
    color: rgb(145, 20, 3);
}

.post-head{
    font-family: 'Staatliches', cursive;
    font-weight: 300;
}

.post{
    margin: 2rem auto;
    padding: 2rem;
    width: 80%;
    background-color: rgba(255, 255, 255, 0.4);
    border-radius: 1.5rem;
    font-size: 2.5rem;
    text-align: justify;
}

.user{
    text-align: left;
    margin: 1rem 0;
    font-size: 2rem;
}

.header-btns{
    float: right;
}

.header-btns img{
    margin: 0.5rem;
}

.footer-btns{
    display: flex;
}

.footer-btns div{
    margin: 1rem 3rem 0.5rem 1.5rem;
    font-size: 2rem;
}

.footer-btns img{
    height: 2rem;
}


.Signup , .Signin{
    text-align: center;
    display: inline-block;
    flex-direction: column;
    margin: 2rem auto;
    padding: 2rem;
    width: 60%;
    background-color: #F78888;
    border-radius: 3rem;
}


.fields{
    font-size: 2rem;
    list-style-type: none;
}

.fields li{
    padding-bottom: 2rem;
    color: #334747;
    color: white;
}

input{
    border: 0.2rem solid white;
    border-radius: 0.8rem;
    color: #334747b9;
    padding: 0.35rem;
    opacity: 0.8;
}

input:focus{
    outline: none;
    opacity: 1.0;
    background-color: rgba(255, 255, 255, 0.600);
}

#submit-button{
    border: 0.4rem solid white;
    border-radius: 2rem;
    background-color: #F78888;
    color: white;
    font-weight: bold;
    width: 10rem;
    height: 5rem;
    opacity: 1.0;
}

#submit-button:hover{
    border: 0.3rem solid #F78888;
    background-color: white;
    color: #F78888;
    opacity: 0.9;
}

#submit-button:active{
    border: 0.3rem solid #F78888;
    background-color: white;
    color: #F78888;
    padding: 0.4rem;
    -webkit-animation: shrink-press 0.5s ease-in;
            animation: shrink-press 0.5s ease-in; 
}

@-webkit-keyframes shrink-press {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
        opacity: 0.9;
    }

    20% {
        -webkit-transform: scale(0.85);
                transform: scale(0.85);
        opacity: 1.0;
    }

    70% {
        opacity: 1.0;
    }

    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
        opacity: 0.9;
    }
}

@keyframes shrink-press {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
        opacity: 0.9;
    }

    20% {
        -webkit-transform: scale(0.85);
                transform: scale(0.85);
        opacity: 1.0;
    }

    70% {
        opacity: 1.0;
    }

    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
        opacity: 0.9;
    }
}

.alert{
    color: rgb(3, 145, 3);
    font-size: 2rem;
    background-color: rgba(144, 204, 244, 0.50);
}

@media only screen and (max-width: 500px){
    input{
        width: 90%;
    }
}
